<template>
<body>
  <h2>Course Information Discoverer</h2>
    <div class="group">
      <div id="select">
        <div class="ui label">University State</div>
        <select class="uidropdownstate" v-model="uniState1" id="university-states">
         <option v-for="option in universityStates" :key="option.id" v-bind:value="option">
            {{ option }}
          </option>
        </select>
      </div>
      
      <div id="select">
        <div class="ui label">University State</div>
        <select class="uidropdownstate" v-model="uniState2" id="university-states">
         <option v-for="option in universityStates" :key="option.id" v-bind:value="option">
           {{ option }}
         </option>
       </select>
      </div>
    </div>
    
    <div class="group">
      <div id="select">
        <div class="ui label">University</div>
        <select class="uidropdownuni" id="universities" v-model="university1" style="float:left">
         <option v-for="option in filteredUnis1" :key="option.id" v-bind:value="option.university">
           {{ option.university }}
         </option>
       </select>  
      </div>
      
      <div id="select">
        <div class="ui label">University</div>
        <select class="uidropdownuni" id="universities" v-model="university2">
         <option v-for="option in filteredUnis2" :key="option.id" v-bind:value="option.university">
           {{ option.university }}
         </option>
       </select>  
      </div>
    </div>
    
    <div class="group">
      <div id="select">
       <div class="ui label">Course</div>
       <select class="uidropdowncourse" id="courses" v-model="course1">
         <option v-for="option in filteredCourses1" :key="option.id" v-bind:value="option.title">
           {{ option.title }}
         </option>
       </select>
      </div>
      
     <div id="select">
       <div class="ui label">Course</div>
       <select class="uidropdowncourse" id="courses" v-model="course2">
         <option v-for="option in filteredCourses2" :key="option.id" v-bind:value="option.title">
           {{ option.title }}
         </option>
       </select>
      </div>
    </div>
  
  <div id = "chart">
   <GChart
      type="LineChart"
      :options="options"
      :data="collectionData"
    /> 
  </div>
</body>

</template>
 
<script>
import { GChart } from "vue-google-charts";
import { getAPI } from '../axios-api'

export default {
  name: "App",
  components: {
    GChart
  },
  data() {
    return {
      collectionData: [
        ["Day", "Course1", "Course2"],
        [1,  7950, 14500],
        [2,  7950, 14500],
        [3,  7950, 14500],
        [4,  0, 14500],
        [5,  0 , 0],
      ],
      /*Array to store api data */
      APIData: [],
      /*declare variables to store v-models*/
      uniState1: null,
      uniState2: null,
      university1: null,
      university2: null,
      course1: null,
      course2: null,
      options: {
        chart: {
          title: "Fees to be paid over years"
        },
        hAxis: {title: 'Number of Years'},
        vAxis: {title: 'Fees in $'},
        width: 800,
        height: 400
      }
    };
  },
  methods: {
  },
  computed: {
    universityStates () {
      return [...new Set(this.APIData.map(({ state }) => state))] /* returns array of unique states from API Data */
    },
    universities () {
      return [...new Set(this.APIData.map(({ university }) => university))] /* returns array of unique universities from API Data */
    },
    filteredUnis1() {
      var filtered = this.APIData.filter(option => !option.state.indexOf(this.uniState1)) /*returns filtered list of unis depending on state chosen*/
      return filtered
    },
    filteredUnis2() {
      var filtered = this.APIData.filter(option => !option.state.indexOf(this.uniState2)) 
      return filtered
    },
    filteredCourses1() {
      return this.APIData.filter(option => !option.university.indexOf(this.university1)) /*returns filtered list of courses depending on uni chosen*/
    },
    filteredCourses2() {
      return this.APIData.filter(option => !option.university.indexOf(this.university2))
    }
  },
  created () {
      getAPI.get('/api/courses/',)
         .then(response => {
           console.log('Course API has received data')
           this.APIData = response.data
         })
         .catch(err => {
           console.log(err)
         })
  }
};
</script>

<style>
.uidropdownstate{
  width: 50px;
} 
.uidropdownuni{
  width: 300px;
}
.uidropdowncourse{
  width: 450px;
}

#select{
  display:inline-block;
  padding: 10px;
}

</style>
